import React, { FC } from 'react';
import { useState } from 'react';
import './input.css';
import { useFormState } from 'react-hook-form';


type AllProps = Condition & InputProps;


type Condition = {
    disabled?: boolean, 
    success?: boolean,
    required:boolean
    validate: Function | object
}

type InputProps = {
    id: string,
    label: string,
    value?: string,
    placeholder: string,
    
    onChange: (str: string) => void,
    size?: 'large' | 'medium' | 'small';
}


export const Input = ({
    id,
    label = '',
    placeholder = '',
    disabled = false,
    onChange,
    value,
    size = 'medium',
    ...props
}: AllProps, ) => {
    const [values, handleChange] = useFormState<InputProps>({
        label: ""
      });
    
      const handleSubmit = (evt: InputProps) => {
        evt.preventDefault();
        console.log("SUBMITTED");
      };
    return (
        <>
            <label className='input--label'>
                {label}
            </label>
            <input
                id={id}
                className={`input--${size}` }
                disabled={disabled}
                onChange={e => onChange(e.target.value)}
                value={value}
                placeholder={placeholder}
                {...props} />
        </>
    )
}

export default Input